import React, {useCallback, useEffect, useState} from 'react';
import styled from "styled-components";
import VideoQueueApi from "../api/VideoQueueApi";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import {useHistory} from "react-router-dom";
import CardActions from "@material-ui/core/CardActions";
import IconButton from "@material-ui/core/IconButton";
import Block from '@material-ui/icons/Block';
import Play from '@material-ui/icons/PlayArrow';
import PlaylistAdd from '@material-ui/icons/PlaylistAdd';
import VideoContentApi from '../api/VideoContentApi';

import thumbnail from '../static/image/me.jpeg';
import VideoWatchApi from "../api/VideoWatchApi";

const DeleteIcon = styled(Block)`
  color: red;
`;

const PlayIcon = styled(Play)`
  color: green;
`;

const AddIcon = styled(PlaylistAdd)`
  color: rgb(152, 118, 170);
`;

const CardTitle = styled(CardHeader)`
  && {
    margin: 0;
    padding: 0 10px;    
  }

  & .MuiCardHeader-title {
    font-size: 1.2rem;
    font-weight: 400;
  }
`;

const CardThumbnail = styled(CardMedia)`
  display: flex;
  flex-direction: column;
  width: 100px;
  min-width: 100px;
  max-width: 100px;
  height: 100px;
  min-height: 100px;
  max-height: 100px;
`;

const CardControls = styled(CardActions)`
  margin-top: 5px;
`;

const Sidebar = styled.div`
  margin: 0;
  padding: 0;
  width: 100%;
`;

const Title = styled.h4`
  margin: 20px 10px;
  padding: 0;
  text-transform: lowercase;
  color: rgb(204, 120, 50);
`;

const VideoCard = styled(Card)`
  && {
    display: flex;
    align-items: flex-start;
    flex-wrap: nowrap;
    content: 1 0 auto;
    padding: 10px;
    min-height: 100px;
    border-radius: 0;
    background: #000;
    color: #a9b7c6;
  }
`;

const CardContent = styled.div`
  display: flex;
  flex-direction: column;
`;

const VideoList = styled.div`
`;

const Icon = styled(IconButton)`
  && {
    color: #a9b7c6;
  }
`;

function VideoSidebar(props) {
  const history = useHistory();

  const video = props.video;

  const queue = VideoQueueApi.list().filter(url => VideoWatchApi.list().indexOf(url) === -1);
  const recommended = listRecommended(video, queue);

  const [, updateState] = useState();
  const forceUpdate = useCallback(() => updateState({}), []);

  return (
    <Sidebar>
      {queue.length > 0 && <Title>Watch Next</Title>}
      <VideoList>
        {queue.map((videoUrl, index) => getWatchNext(video, videoUrl, index, history, forceUpdate))}
      </VideoList>
      {recommended.length > 0 && <Title>Recommended</Title>}
      <VideoList>
        {recommended.map((videoUrl, index) => getRecommended(video, videoUrl, index, forceUpdate))}
      </VideoList>
    </Sidebar>
  );
}

function listRecommended(video, queue) {
  return video.recommended.filter(item => queue.indexOf(item) === -1);
}

function getWatchNext(video, videoUrl, index, history, forceUpdate) {
  const nextVideo = VideoContentApi.load(videoUrl);
  return (
    <VideoCard key={`${videoUrl}-${index}`}>
      <CardThumbnail image={nextVideo.thumbnail || thumbnail} />
      <CardContent>
        <CardTitle title={nextVideo.title} />
        <CardControls disableSpacing>
          <Icon onClick={() => {
            VideoQueueApi.removeTo(index + 1);
            forceUpdate();

            history.push(`/cv/${videoUrl}`);
          }}>
            <PlayIcon />
          </Icon>
          <Icon onClick={() => {
            VideoQueueApi.remove(index);
            forceUpdate();
          }}>
            <DeleteIcon />
          </Icon>
        </CardControls>
      </CardContent>
    </VideoCard>
  );
}

function getRecommended(video, videoUrl, index, forceUpdate) {
  const recommendedVideo = VideoContentApi.load(videoUrl);
  return (
    <VideoCard key={`${videoUrl}-${index}`}>
      <CardThumbnail image={thumbnail.thumbnail || thumbnail} />
      <CardContent>
        <CardTitle title={recommendedVideo.title} />
        <CardControls disableSpacing>
          <Icon onClick={() => {
            VideoQueueApi.add(videoUrl);
            forceUpdate();
          }}>
            <AddIcon />
          </Icon>
        </CardControls>
      </CardContent>
    </VideoCard>
  );
}

export default VideoSidebar;