import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from "react-router-dom";
import Header from "../component/Header";
import VideoContentApi from "../api/VideoContentApi";
import Video from "../component/Video";
import styled from "styled-components";
import Grid from "@material-ui/core/Grid";
import VideoSidebar from "../component/VideoSidebar";
import VideoQueueApi from "../api/VideoQueueApi";
import Chip from "@material-ui/core/Chip";
import VideoWatchApi from "../api/VideoWatchApi";

const Title = styled.h2`
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1.8rem; 
`;

const Subtitle = styled.small`
  font-size: 0.8rem;
  font-weight: 300;
  color: rgb(128, 128, 128);
`;

const VideoBar = styled.div`
  position: relative;
  margin: 0;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.1);
`;

const VideoFrame = styled.div`
  position: sticky;
  top: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 100vh;
`;

const Tag = styled(Chip)`
  && {
    margin-right: 10px;
    padding: 0 3px;
    font-weight: 400;
    background-color: rgb(128, 128, 128, 0.3);
    color: #999;
  }
`;

function VideoPage() {
  const {title} = useParams();
  const history = useHistory();

  const video = VideoContentApi.load(title);
  if (video === undefined) {
    history.push('/cv');
    return;
  }

  VideoWatchApi.watch(video.url);

  console.log('watch list', video.url, VideoWatchApi.list());

  if (video.next && video.next.length > 0) {
    video.next.forEach(videoUrl => VideoQueueApi.add(videoUrl));
  }

  const subtitle = video.subtitle ? <Subtitle>{video.subtitle}</Subtitle> : null;

  const tags = (video.tags || []).map(tag => <Tag key={tag} label={tag} size="small" />)

  return (
    <div>
      <Grid container spacing={0}>
        <Grid item xs={12} sm={12} md={9} lg={9} xl={10}>
          <VideoFrame>
            <Header />
            <Video src={video.file} onEnded={() => next(history)} />
            <VideoBar>
              <Title>{video.title} {subtitle} {tags}</Title>
            </VideoBar>
          </VideoFrame>
        </Grid>
        <Grid item xs={12} sm={12} md={3} lg={3} xl={2}>
          <VideoSidebar video={video} />
        </Grid>
      </Grid>
    </div>
  );
}

function next(history) {
  if (VideoQueueApi.isEmpty()) {
    return;
  }

  const nextVideoUrl = VideoQueueApi.pop();
  history.push(`/cv/${nextVideoUrl}`);
}

export default VideoPage;
