const queue = [];

const VideoQueueApi = {
  add: function(url) {
    if (queue.indexOf(url) === -1) {
      queue.push(url);
    }
  },
  pop: function() {
    return queue.shift();
  },
  isEmpty: function() {
    return queue.length === 0;
  },
  list: function() {
    return queue.slice();
  },
  remove: function(index) {
    queue.splice(index, 1);
  },
  removeTo: function(index) {
    queue.splice(0, index);
  }
};

export default VideoQueueApi;